.swiper-wrapper {
  display: flex;
  margin: auto;
}

.swiper-slide {
  margin: auto;
}

.swiper {
  &__demo-foto {
    width: 100%;
  }
}

.swiper-btnnext {
  position: absolute;
  left: 50px;
  bottom: -50px;
  cursor: pointer;
  margin: 0;
  padding: 0;
  z-index: 1;
  outline: none;
  height: fit-content;

  @media (min-width: 768px) {
    
  }

  &--color {
    width: 40px;
    height: 20px;
    fill: $black;

    @media (min-width: 768px) {
      
    }
  }

  &--color:hover {
    fill: $green;
  }
}

.swiper-btnprev {
  position: absolute;
  left: 0;
  bottom: -50px;
  cursor: pointer;
  margin: 0;
  padding: 0;
  outline: none;
  height: fit-content;
  z-index: 1;

  @media (min-width: 768px) {
   
  }

  &--color {
    width: 40px;
    height: 20px;
    fill: $black;

    @media (min-width: 768px) {
      
    }
  }

  &--color:hover {
    fill: #2FA06A;
  }
}

.swiper-btnnext.swiper-button-disabled {
  opacity: .35;
  cursor: default;
} 

.swiper-btnprev.swiper-button-disabled {
  opacity: .35;
  cursor: default;
}

